<template>
  <va-card :title="$t('tables.labelAccount')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="ml-0"
      @click="showCreateModal()"
    >
    </va-button>

    <div class="row">
      <tree-view
        :tree.sync="tree"
        :editable="true"
        class="flex md6 sm12 fitContentHight"
        dir="ltr"
      ></tree-view>
      <div class="flex md6 sm12">
        <va-input
          :value="term"
          :placeholder="$t('tables.searchByName')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
        <va-data-table
          :fields="fields"
          :data="filteredData"
          :loading="loading"
          :per-page="10"
          api-mode
          hoverable
        >
          <template slot="marker" slot-scope="props">
            {{ props.rowIndex + 1 }}
          </template>
          <template slot="actions" slot-scope="props">
            <va-button
              flat
              small
              color="gray"
              @click="showEditModal(props.rowData)"
              class="ma-0"
            >
              {{ $t("tables.edit") }}
            </va-button>

            <va-button
              flat
              small
              color="danger"
              @click="showDeleteModal(props.rowData)"
              class="ma-0"
            >
              {{ $t("tables.delete") }}
            </va-button>
          </template>
        </va-data-table>

        <va-modal
          v-model="deleteModal"
          no-outside-dismiss
          :title="$t('generic.deleteItemTitle')"
          :okText="$t('modal.delete')"
          :cancelText="$t('modal.cancel')"
          :message="$t('generic.deleteItemMsg')"
          @ok="okDeletefun()"
          @cancel="cancleDeletefun()"
        >
        </va-modal>
        <va-modal
          v-model="createModal"
          no-outside-dismiss
          size="large"
          :title="$t(modelTitle)"
          hide-default-actions
        >
          <form @submit.prevent="editModal ? okEditfun() : okCreatefun()">
            <div class="row flex-center">
              <div class="flex md6 sm12 xs12 px-1">
                <label class="input-label"> {{ $t("placeholder.name") }} </label>
                <va-input
                  v-model="currentobject.name"
                  :error="riquierdvalidation(currentobject.name)"
                  :error-messages="$t('errorMessage.required')"
                  :placeholder="$t('placeholder.name')"
                  required
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-date-picker
                  :label="$t('placeholder.date')"
                  v-model="currentobject.date"
                  required
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-select
                  :label="$t('placeholder.centralCostAccount')"
                  v-model="centralCostAccount.value"
                  :error="riquierdvalidation(centralCostAccount.value)"
                  :error-messages="['error message']"
                  required
                  searchable
                  textBy="name"
                  keyBy="id"
                  :options="centralCostAccount.options"
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-select
                  :label="$t('placeholder.accountsNature')"
                  v-model="accountsNature.value"
                  :error="riquierdvalidation(accountsNature.value)"
                  :error-messages="['error message']"
                  required
                  searchable
                  textBy="name"
                  keyBy="id"
                  :options="accountsNature.options"
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-checkbox
                  :label="$t('placeholder.isMain')"
                  v-model="currentobject.is_main"
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-select
                  :label="$t('placeholder.Parent')"
                  v-model="parants.value"
                  searchable
                  textBy="name"
                  keyBy="id"
                  :options="parants.options"
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <va-select
                  :label="$t('placeholder.finalAccounts')"
                  v-model="finalAccounts.value"
                  searchable
                  textBy="name"
                  keyBy="id"
                  :options="finalAccounts.options"
                />
              </div>
            </div>
            <div class="d-flex justify--center mt-3">
              <va-button
                type="button"
                color="gray"
                flat
                @click="editModal ? cancleEditfun() : cancleCreatefun()"
              >
                {{ $t("modal.cancel") }}
              </va-button>
              <va-button type="submit" :disabled="stopBtn" class="my-0">{{
                $t(modelOktext)
              }}</va-button>
            </div>
          </form>
        </va-modal>
      </div>
    </div>
  </va-card>
</template>

<script>
import AccountsService from '../../../../services/finances/AccountsService'
import CentralCostAccountService from '../../../../services/finances/Central_Cost_AccountService'
import AccountsNatureService from '../../../../services/finances/AccountsNatureService'
import { debounce } from 'lodash'
import helper from '../../../../helper/helper'

export default {
  data () {
    return {
      term: null,
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      tree: [],
      dataTable: [],
      parants: {
        value: '',
        options: [],
      },
      accountsNature: {
        value: '',
        options: [],
      },
      centralCostAccount: {
        value: '',
        options: [],
      },
      finalAccounts: {
        value: '',
        options: [],
      },
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {
        date: '',
        is_main: true,
      },
    }
  },
  computed: {
    fields () {
      return [
        {
          name: '__slot:marker',
          width: '30px',
          dataClass: 'text-center',
          title: '#',
        },
        {
          name: 'name',
          title: this.$t('tables.headings.name'),
        },
        {
          name: '__slot:actions',
          dataClass: 'text-right',
        },
      ]
    },
    filteredData () {
      if (!this.term || this.term.length < 1) {
        return this.dataTable
      }

      return this.dataTable.filter((item) => {
        return item.name.toLowerCase().includes(this.term.toLowerCase())
      })
    },
  },
  created () {
    this.readItemstree()
    this.readItems()
    this.getAccountsNature()
    this.getcentralCostAccountService()
    this.getAllFinalAccounts()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    search: debounce(function (term) {
      this.term = term
    }, 400),
    /*  api Padination */
    readItemstree () {
      AccountsService.GetAllTreeAcc().then(
        (response) => {
          this.tree = response.data
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    readItems () {
      AccountsService.getAll().then(
        (response) => {
          this.dataTable = response.data
          this.parants.options = response.data
          this.loading = false
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    getAccountsNature () {
      AccountsNatureService.getAll().then(
        (response) => {
          this.accountsNature.options = response.data
          console.log(response.data)
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    getcentralCostAccountService () {
      CentralCostAccountService.getAll().then(
        (response) => {
          this.centralCostAccount.options = response.data
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    getAllFinalAccounts () {
      AccountsService.getAllFinalAccounts().then(
        (response) => {
          console.log('final account', response)
          this.finalAccounts.options = response.data
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    create () {
      if (
        this.accountsNature.value === '' ||
        this.centralCostAccount.value === '' ||
        this.finalAccounts.value === '' ||
        this.accountsNature.value === null ||
        this.centralCostAccount.value === null ||
        this.finalAccounts.value === null
      ) {
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      if (this.parants.value !== '') {
        this.currentobject.fK_ParentCode = this.parants.value.id
      }

      if (this.accountsNature.value !== '') {
        this.currentobject.fK_Accounts_Nature = this.accountsNature.value.id
      }
      if (this.centralCostAccount.value !== '') {
        this.currentobject.fK_Central_Cost_Account = this.centralCostAccount.value.id
      }
      if (this.finalAccounts.value !== '') {
        this.currentobject.fK_FinalAccount = this.finalAccounts.value.id
      }

      AccountsService.create(this.currentobject).then(
        (response) => {
          if (response.headers['x-status'] === 'true') {
            this.readItemstree()
            this.readItems()
            this.loading = false
            this.currentobject = {
              date: '',
              is_main: true,
            }
            this.parants.value = ''
            this.accountsNature.value = ''
            this.centralCostAccount.value = ''
            this.finalAccounts.value = ''
            this.createModal = false
            this.stopBtn = false
            this.showToast(this.$t('generic.done'), {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 2500,
              type: 'success',
            })
          } else {
            this.loading = false
            this.stopBtn = false
            this.showToast(this.$t('generic.error'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    edit (user) {
      if (
        this.accountsNature.value === '' ||
        this.centralCostAccount.value === '' ||
        this.finalAccounts.value === '' ||
        this.accountsNature.value === null ||
        this.centralCostAccount.value === null ||
        this.finalAccounts.value === null
      ) {
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      if (this.parants.value !== '') {
        this.currentobject.fK_ParentCode = this.parants.value.id
      }

      if (this.accountsNature.value !== '') {
        this.currentobject.fK_Accounts_Nature = this.accountsNature.value.id
      }
      if (this.centralCostAccount.value !== '') {
        this.currentobject.fK_Central_Cost_Account = this.centralCostAccount.value.id
      }
      if (this.finalAccounts.value !== '') {
        this.currentobject.fK_FinalAccount = this.finalAccounts.value.id
      }
      AccountsService.update(this.currentobject.id, this.currentobject).then(
        (response) => {
          if (response.headers['x-status'] === 'true') {
            this.readItemstree()
            this.readItems()
            this.currentobject = {
              date: '',
              is_main: true,
            }
            this.parants.value = ''
            this.accountsNature.value = ''
            this.centralCostAccount.value = ''
            this.finalAccounts.value = ''

            this.loading = false
            this.editModal = false
            this.createModal = false
            this.stopBtn = false
            this.showToast(this.$t('generic.done'), {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 2500,
              type: 'success',
            })
          } else {
            this.loading = false
            this.stopBtn = false
            this.editModal = false
            this.createModal = false
            this.showToast(this.$t('generic.error'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    remove (id) {
      AccountsService.delete(id).then(
        (response) => {
          if (response.headers['x-status'] === 'true') {
            const idx = this.dataTable.findIndex((u) => u.id === id)
            this.dataTable.splice(idx, 1)
            this.loading = false
            this.showToast(this.$t('generic.done'), {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 2500,
              type: 'success',
            })
          } else {
            this.loading = false
            this.showToast(this.$t('generic.error'), {
              icon: 'fa-exclamation',
              position: 'bottom-center',
              duration: 2500,
              type: 'error',
            })
          }
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.loading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
      this.stopBtn = false
    },
    /* end Delete model */
    /* edit model */
    showEditModal (cuobject) {
      this.currentobject.is_main = cuobject.is_main
      if (cuobject.parentCode !== null && cuobject.parentCode !== undefined) {
        this.parants.value = cuobject.parentCode
      }
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      if (
        cuobject.centralCostAccount !== null &&
        cuobject.centralCostAccount !== undefined
      ) {
        this.centralCostAccount.value = cuobject.centralCostAccount
      }
      if (cuobject.accountsNature !== null && cuobject.accountsNature !== undefined) {
        this.accountsNature.value = cuobject.accountsNature
      }
      if (cuobject.finalAccounts !== null && cuobject.finalAccounts !== undefined) {
        this.finalAccounts.value = cuobject.finalAccounts
      }
      this.createModal = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.editModal = false
      this.createModal = false
      this.loading = false
      this.stopBtn = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {
        date: '',
        is_main: true,
      }
      this.parants.value = ''
      this.accountsNature.value = ''
      this.centralCostAccount.value = ''
      this.finalAccounts.value = ''
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.currentobject.date = helper.todayDate()
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.stopBtn = false
      this.currentobject = {
        date: '',
        is_main: true,
      }
      this.parants.value = ''
      this.accountsNature.value = ''
      this.centralCostAccount.value = ''
      this.finalAccounts.value = ''
    },
    /* end create model */
  },
}
</script>

<style lang="scss">
i.fa.fa-exclamation {
  color: red !important;
}

.fitContentHight {
  height: fit-content;
}
</style>
