import http from '../../http-common'

class AccountsNatureSercice {
  getAll () {
    return http.get('AccountsNature')
  }

  getAllPagnation (params) {
    return http.get('AccountsNaturePaging', { params })
  }

  get (id) {
    return http.get(`AccountsNature/getById?Id=${id}`)
  }

  search (params) {
    return http.get('AccountsNature/Search', { params })
  }

  create (data) {
    return http.post('AccountsNature', data)
  }

  update (id, data) {
    return http.put(`AccountsNature?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`AccountsNature?Id=${id}`)
  }
}

// eslint-disable-next-line new-cap
export default new AccountsNatureSercice()
